.dancing {
  font-family: 'Dancing Script', cursive;
}

body {
  min-height: 100vh;
}

.full-screen {
  width: 100%;
  height: 100vh;
}

.section-bg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.charmonman {
  font-family: 'Charmonman', cursive;
}

.section-head {
  .text-content {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;
    width: 100%;
    color: #fff;
    font-size: 1.5em;
  }

  .overlay {
    background-color: hsla(0, 0%, 0%, 0.6);
  }

  h1 {
    margin-bottom: 0;
  }

  .line {
    position: absolute;
    width: 40%;
    height: 1px;
    background: #fff;
    top: 50%;

    &.left {
      right: 60%;
    }

    &.right {
      left: 60%;
    }
  }
}

.section-image {
  overflow: hidden;

  .section-bg {
    position: absolute;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    opacity: 0.75;
    filter: blur(10px);
  }

  .image-wrapper {
    position: absolute;
    height: 80%;
    top: 50%;
    transform: translateY(-50%);
    left: 20%;

    img {
      height: 100%;
      border: 1px solid #fff8
    }
  }

  .overlay {
    background: hsla(0, 0%, 0%, 0.4);
  }

  .text-content {
    h2 {
      font-size: 2.5em;
    }
  }
}

.section-intro {
  .text-content {
    color: #fff;
    position: absolute;
    right: 20%;
    width: 30%;
    padding-left: 5%;
    top: 50%;
    top: 50%;
    transform: translateY(-50%);
    font-family: 'Montserrat', sans-serif;
  }
}

.section-list {
  .text-content {
    position: relative;
    text-align: center;
    color: #fff;
    top: 50%;
    transform: translateY(-50%);
    font-family: 'Montserrat', sans-serif;
  
    ul {
      font-size: 1.5em;
      list-style: none;
      padding: 0;
    }
  }
}

.section-slide {
  .text-content {
    position: absolute;
    width: 100%;
    text-align: center;
    top: 20%;
  }

  img {
    width: 100%;
    border: 1px solid #fff;
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  }

  ul {
    position: absolute;
    bottom: 0%;
    left: 10%;
    right: 10%;
    top: 10%;
    margin: 0;
    padding: 0;
  }

  li {
    position: absolute;
    list-style: none;
    width: 35%;
    left: 15%;
    bottom: 0;
    transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);

    &:nth-child(1) {
      transform: translate(70%, -35%) rotate(10deg);
    }

    &:nth-child(2) {
      transform: translate(50%, -25%) rotate(7deg);
    }

    &:nth-child(3) {
      transform: translate(30%, -15%) rotate(3deg);
    }

    &:nth-child(4) {
      transform: translate(0%, -5%) rotate(-10deg);
    }

    &.show {
      z-index: 100;
      width: 80%;
      left: 10%;
      transform: translate(0, -5%) rotate(0deg);
    }
  }

  .text-content {
    text-align: center;
    font-family: 'Charmonman', cursive;
  }
}